.container{
    padding-top: 50px;
    border-top: 50px;
}

.header {
    height: 80px;
    padding-top: 5px;
    background-color: #ce0505;
}


.rectangle {
    width: 160px;
    height: 40px;
    border-radius: 7px;
    border: solid 1px #ffffff;
    float: right;
    margin-right: 20%;
    margin-top: -2%;
}

.account {
    width: 152px;
    height: 23px;
    font-family: Poppins;
    font-size: 16px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.56;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff;
}

.main-heading {
    height: 70px;
    font-family: Poppins;
    font-size: 36px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.53;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
    margin: 2%;
}

.Rectangle-5 {
    height: 700px;
    -webkit-box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: #ffffff;
    margin-left: 10%;
}

.Filters {
    height: 25px;
    font-family: Poppins;
    font-size: 22px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.5;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
    padding-left: 6%;
}

.Select-Location {
    height: 40px;
    font-family: Poppins;
    font-size: 20px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.5;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
    margin-left: 7%;
}

.textbox {
    width: 208px;
    height: 35px;
    border-radius: 3px;
    border: solid 1px #8c96ab;
    margin-right: 13%;
}

.Cost-For-Two {
    height: 45px;
    font-family: Poppins;
    font-size: 18px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 3.5;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
    margin-left: 7%;
}

.Sort {
    height: 45px;
    font-family: Poppins;
    font-size: 18px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 3.5;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
    margin-left: 7%;
}

.item {
    width: 800px;
    height: 275px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: #f2eded;
    margin-bottom: 20px;
    border-radius: 25px;
}


.option-item {
    font-family: Poppins;
    font-size: 15px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.8;
    letter-spacing: normal;
    text-align: left;
    color: #8c96ab;
    margin-right: 100px;
}

.block {
    display: block;
}

.inline-block {
    display: inline-block;
}

.pagination>.active>a, .pagination>.active>a:focus, .pagination>.active>a:hover, .pagination>.active>span, .pagination>.active>span:focus, .pagination>.active>span:hover {
    z-index: 3;
    color: #fff;
    cursor: default;
    background-color: #8c96ab;
    border-color: #8c96ab;
}

.hotel_name {
    width: 100%;
    height: 43px;
    font-family: Poppins;
    font-size: 25px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 2;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
}

.image {
    width: 100%;
    height: 275px;
    border-radius: 23px;
    background-color: #ea6060;
}

.city_name {
    width: 100%;
    height: 30px;
    font-family: Poppins;
    font-size: 16px;
    font-style: normal;
    line-height:3;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
}

.address-text {
    width: 100%;
    height: 23px;
    font-family: Poppins;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.88;
    letter-spacing: normal;
    text-align: left;
    color: #636f88;
}

.row-height {
    height: 60%;
    padding-top: 4%;
}

.cuisine_block {
    font-family: Poppins;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.88;
    letter-spacing: normal;
    text-align: left;
    color: #636f88;
    padding-left: 25%;
}

.cuisine_out {
    font-family: Poppins;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.88;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
}

.colhover:hover{
    background-image: -webkit-linear-gradient(top, #c9601a, #f5ae43);
    height:275px;
    float:bottom;
    border-radius: 25px;
    opacity: .9;
    animation-name: slide;
    animation-duration: 0.5s;
    transition: all .2s ease-out;
    transform : translate(-15px,0);
}


@keyframes slide{
    0%{opacity:0;}
    100%{opacity: .9;}
}