  .top-navbar{
	position: relative;
    z-index: 10;

}
.navbar{
    background-color: #f9fbfc;
}

.top-navbar .navbar{
	padding: 10px 0px;
	position: fixed;
	width: 100%;
	border-radius: 0px 0px 100px 100px;
	transition: height .3s ease-out, background .3s ease-out, box-shadow .3s ease-out;
	-webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
	-webkit-box-shadow: 0 8px 6px -6px rgba(0, 0, 0, 0.4);
	box-shadow: 0 8px 6px -6px rgba(0, 0, 0, 0.4);
    z-index: 100;
}

#mynavbar1{
    color: #c9601a;
    font-size: 35px;
    padding-left: 100px;
	border-radius: 4px;
    font-style: inherit;
    font-family: "Sofia";
    
}
.mynavbar2{
    font-size: 16px;
    padding-left: 10px;
    padding-right: 10px;
	border-radius: 4px;
	text-transform: uppercase;
}

.mynavbar2:hover{
    background-image: -webkit-linear-gradient(top, #c9601a, #f5ae43);
}