body{
    background-color: #E4E9FD;
  background-image: -webkit-linear-gradient(45deg, #A683E3 50%, #E4E9FD 50%);
  min-height: 1000px;
  font-family: 'helvetica neue';
}
.imageContainer{
    border-style:solid;
    border-width:2px;
    width: 100%;
    height: 90vh;
    animation: animate 16s ease-in-out infinite;
    background-size: cover;
    border-radius: 25px;
    
}
@keyframes animate{
    0%,100%{
        background-image: url('/images/slider-01.jpg');
    }
    25%{
        background-image: url('/images/foodwallpaper.png');
    }
    50%{
        background-image: url('/images/slider-02.jpg');
    }
    75%{
        background-image: url('/images/slider-03.jpg');
    }
}
.imagecontainer2{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 90vh;
    background: rgba(0,0,0,0.5);
    border-radius: 25px;
}
#logo{
    text-align:center;
    font-size:60px;
    margin:auto;
    border-radius:50%;
    margin-top:80px;
    margin-bottom: 180px;
}

.heading{
    text-align:center;
    color: rgb(211, 211, 9);
    font-size:2em;
}
.heading span{
    color: rgb(252, 7, 7);
}

.locationSelector{
    margin-top:20px;
    margin-bottom:40px;
    text-align:center;
    
}

.locationDropDown{
    width:300px;
    height:60px;
    font-size:20px;
    border:none;
    background-image: -webkit-linear-gradient(top, #e4f6f8, #e9f1f7);
    border-radius: 28px;
    font-family: Georgia;
    padding: 10px 20px 10px 20px;
    text-decoration: none;
    
}

.locationDropDown:hover {
    background-image: -webkit-linear-gradient(top, #c9601a, #f5ae43);
  }
  

.restinput{
    width:400px;
    height:60px;
    border-radius:2%;
    font-size:20px;
    border:none;
}

  
 